.hero-carousel-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 2rem;
  background: linear-gradient(135deg, #faf9f7 0%, #f5f5f0 100%);
}

.carousel-track {
  display: flex;
  gap: 1.5rem;
  padding: 2rem;
  animation: scroll 30s linear infinite;
  height: 100%;
  align-items: center;
}

.carousel-item {
  min-width: 200px;
  width: 200px;
  height: 280px;
  border-radius: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
  transition: transform 0.3s ease;
}

.carousel-item:hover {
  transform: scale(1.05);
}

.movie-poster {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  text-align: center;
}

.poster-symbol {
  font-size: 3.5rem;
}

.poster-title {
  font-size: 0.95rem;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.7);
  margin: 0;
  word-break: break-word;
  max-width: 90%;
}

.carousel-overlay-left {
  position: absolute;
  left: 0;
  top: 0;
  width: 100px;
  height: 100%;
  background: linear-gradient(90deg, rgba(250, 249, 247, 0.8) 0%, transparent 100%);
  z-index: 10;
  pointer-events: none;
}

.carousel-overlay-right {
  position: absolute;
  right: 0;
  top: 0;
  width: 100px;
  height: 100%;
  background: linear-gradient(270deg, rgba(250, 249, 247, 0.8) 0%, transparent 100%);
  z-index: 10;
  pointer-events: none;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-200px * 8 - 1.5rem * 8));
  }
}

@media (max-width: 768px) {
  .carousel-item {
    min-width: 150px;
    width: 150px;
    height: 220px;
  }

  .poster-symbol {
    font-size: 2.5rem;
  }

  .poster-title {
    font-size: 0.85rem;
  }

  .carousel-overlay-left,
  .carousel-overlay-right {
    width: 50px;
  }

  @keyframes scroll {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(calc(-150px * 8 - 1.5rem * 8));
    }
  }
}
