.auth-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  background: linear-gradient(135deg, rgba(220, 38, 38, 0.05), rgba(245, 158, 11, 0.05));
}

.auth-container {
  width: 100%;
  max-width: 400px;
  background: var(--card);
  padding: 2.5rem;
  border-radius: 1rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  animation: fadeIn 0.6s ease-out;
}

.auth-header {
  text-align: center;
  margin-bottom: 2rem;
}

.auth-header h1 {
  font-size: 2rem;
  color: var(--primary);
  margin-bottom: 0.5rem;
}

.auth-header p {
  color: var(--muted-foreground);
  font-size: 0.95rem;
}

.error-message {
  background: rgba(220, 38, 38, 0.1);
  color: var(--primary);
  padding: 0.75rem;
  border-radius: calc(var(--radius) / 2);
  margin-bottom: 1.5rem;
  font-size: 0.9rem;
  border-left: 3px solid var(--primary);
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-weight: 500;
  color: var(--foreground);
  font-size: 0.9rem;
}

.form-group input {
  padding: 0.75rem;
  border: 2px solid var(--border);
  border-radius: calc(var(--radius) / 2);
  background: var(--input);
  color: var(--foreground);
  font-size: 0.95rem;
  transition: all 0.3s;
}

.form-group input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(220, 38, 38, 0.1);
}

.auth-footer {
  text-align: center;
  margin-top: 1.5rem;
  color: var(--muted-foreground);
  font-size: 0.9rem;
}

.auth-footer a {
  color: var(--primary);
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s;
}

.auth-footer a:hover {
  color: var(--accent);
}

@media (max-width: 480px) {
  .auth-container {
    padding: 1.5rem;
  }

  .auth-header h1 {
    font-size: 1.5rem;
  }
}
