.home-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #faf9f7 0%, #ffffff 100%);
}

.hero-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 4rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 6rem 1.5rem;
  min-height: 90vh;
}

.hero-content {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  z-index: 10;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 800;
  color: var(--primary);
  line-height: 1.1;
}

.hero-subtitle {
  font-size: 1.5rem;
  color: var(--foreground);
  font-weight: 600;
}

.hero-description {
  font-size: 1.1rem;
  color: var(--muted-foreground);
  line-height: 1.6;
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  margin-top: 1rem;
}

.btn {
  padding: 0.875rem 2rem;
  border-radius: var(--radius);
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s;
  display: inline-block;
}

.btn-primary {
  background: var(--primary);
  color: var(--primary-foreground);
  box-shadow: 0 4px 15px rgba(220, 38, 38, 0.2);
}

.btn-primary:hover {
  background: var(--accent);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(220, 38, 38, 0.3);
}

.btn-secondary {
  background: transparent;
  color: var(--primary);
  border: 2px solid var(--primary);
  box-shadow: 0 2px 8px rgba(220, 38, 38, 0.1);
}

.btn-secondary:hover {
  background: var(--primary);
  color: var(--primary-foreground);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(220, 38, 38, 0.2);
}

.authenticated-home {
  padding: 2rem 1.5rem;
  background: linear-gradient(135deg, #faf9f7 0%, #ffffff 100%);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.page-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--foreground);
  margin-bottom: 2rem;
}

@media (max-width: 768px) {
  .hero-section {
    grid-template-columns: 1fr;
    gap: 2rem;
    padding: 3rem 1rem;
    min-height: auto;
  }

  .hero-title {
    font-size: 2.5rem;
  }

  .hero-subtitle {
    font-size: 1.2rem;
  }

  .hero-buttons {
    flex-direction: column;
  }

  .btn {
    width: 100%;
    text-align: center;
  }
}
